.task__reports__container {
  padding: 20px;
  padding-bottom: 40px;
  width: 94%;
  margin: 0 auto;
}

.task__report__nav {
  display: flex;
  gap: 10px;
  margin-bottom: 1rem;
}

.back {
  position: relative;
  width: 40px;
  height: 35px;
  border: none;
  outline: none;
  background-color: white;
  box-shadow: rgb(204, 219, 232) 3px 3px 6px 0px inset,
    rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset;
  border-radius: 5px;
  cursor: pointer;
}

.task__reports__container h6 {
  font-size: 30px;
}

.task__reports__container_header h3 {
  font-size: 2rem;
}

.task__reports__container_header p {
  font-size: 1.2rem;
}

.task__report__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
}

/* Style for the table */
table {
  border-collapse: collapse;
  width: 100%;
  margin-top: 20px;
}

/* Style for table header */
table th {
  background-color: #005734;
  color: #fff;
  font-weight: bold;
  padding: 10px;
  text-align: center;
}

/* Style for table rows */
table tr:nth-child(even) {
  background-color: #f2f2f2;
}

/* Style for table data cells */
table td {
  padding: 10px;
  border-bottom: 1px solid #ddd;
  text-align: center;
}

/* Hover effect on table rows */
table tr:hover {
  background-color: #ddd;
}

.select__task__report__project {
  align-items: center;
  background-color: #fff;
  /* border: 1px solid #005734; */
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  gap: 1rem;
  padding: 0.2rem 0.2rem;
  /* width: -webkit-max-content;
  width: max-content; */
  width: 50%;
}

.select__task__report__project > div {
  width: 100%;
}

.task__select__Project {
  width: 80%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 0.5rem;
}

@media screen and (max-width: 768px) {
  .task__reports__container {
    padding: 0.1rem;
    width: 94%;
  }
  .select__task__report__project {
    width: 100%;
  }
}
