.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(
    0,
    0,
    0,
    0.5
  ); /* Black background with opacity (0.8) */
  z-index: 9999; /* Adjust the z-index value to ensure the overlay appears on top */
  font-family: Poppins;
  .create-new-task {
    box-sizing: border-box;
    background-color: white;
    border: #ccc 1px solid;
    border-radius: 5px;
    padding: 20px 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 15;
    transform: translate(-50%, -50%);
    width: 500px;
    font-family: Poppins;
    max-height: 75%;
    overflow-y: auto;

    @media (max-width: 798px) {
      width: 90%;
    }
    h2 {
      color: #000;
      text-align: left;
      font-size: 21px;
      font-family: Poppins;
      font-style: normal;
      font-weight: 600;
      line-height: 21.284px;
      letter-spacing: 0.288px;
      margin-bottom: 10px;
    }
    label {
      display: block;
      margin-top: 10px;
      color: #000;
      font-size: 15px;
      font-family: Poppins;
      font-style: normal;
      font-weight: 500;
      line-height: 140%;
    }
    .input,
    textarea {
      width: 100%;
      border: #efefef 1px solid;
      border-radius: 5px;
      outline: none;
      padding: 5px 10px;
      max-height: 75px;
      font-family: Poppins;
    }
    .buttons {
      margin-top: 20px;
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
      gap: 15px;
      font-family: Poppins;
      button {
        width: 90px;
        padding: 7px 4px;
        border-radius: 5px;
        outline: none;
        border: none;
        font-weight: 500;
        background-color: #005734;
        color: white;
        cursor: pointer;
        font-family: Poppins;
        &:last-child {
          background-color: #9a9a9a;
        }
      }
    }
    .task-type {
      display: flex;
      background-color: white;
      margin-bottom: 10px;
      gap: 25px;
      font-family: Poppins;
      > div {
        display: flex;
        gap: 5px;
        align-items: center;
        > p {
          white-space: nowrap;
        }
      }
    }
    .create-new-task-member-container {
      background-color: white;
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
      font-family: Poppins;
      div {
        display: flex;
        align-items: center;
        gap: 0px;
        p {
          font-size: 12px;
          white-space: nowrap;
        }
      }
    }

    // SCROLLBAR STYLES FOR CHROME
    &::-webkit-scrollbar {
      width: 8px;
      height: 8px;
    }

    &::-webkit-scrollbar-track {
      background: #efefef;
    }

    &::-webkit-scrollbar-thumb {
      background: #b8b8b8;
    }

    &::-webkit-scrollbar-thumb:hover {
      background: #005734;
    }
  }
}

.added-members-input {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 5px;
  border: #ccc 1px solid;
  border-radius: 5px;
  padding: 2px 2px;
  min-height: 35px;
  > div {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 5px;
    cursor: pointer;
    font-size: small;
    border: #ccc 1px solid;
    border-radius: 5px;
    height: 20px;
    padding: 0 5px;
    min-width: 50px;
  }
  > input {
    width: 150px;
    border: none;
    outline: none;
  }
    button{
      background-color: transparent !important;
      width: fit-content !important;
      color: #ccc  !important;
    }
}
.members {
  gap: 10px;
  font-family: Poppins;
  max-height: 100px;
  overflow: auto;
  > div {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.sub__tasks{
  .input{
    border: 1px solid #efefef  ;
    margin: 0.4rem 0 1rem;
    button{
      border: none;
      background-color: transparent;
      cursor: pointer;
    }
  }
  input{
    border: none;
    display: block;
    outline: transparent;
    border-radius: 4px;
    // margin-bottom: 10px;
    width: 100%;
    font-family: 'Poppins', sans-serif;
    font-size: 0.75rem;
  }
  >.btn{
    display: flex;
    justify-content: flex-start;
    button{
      width: 35px;
      height: 35px;
      border-radius: 50%;
      background-color: #005734;
      color: white;
      border: none;
      outline: transparent;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 600;
      font-size: 20px;
      cursor: pointer;
    }
  }
}
