.share__Overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  backdrop-filter: blur(5px);
  z-index: 9999;
  overflow-y: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.share__Modal {
  width: 40%;
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  flex-direction: column;
  background-color: #fff;
  border-radius: 10px;
  padding: 1rem 2rem 2rem;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  max-height: 85%;
  overflow-y: auto;
  scrollbar-width: thin;
}

.share__Modal::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.share__Modal::-webkit-scrollbar-track {
  background: #efefef;
}

.share__Modal::-webkit-scrollbar-thumb {
  background: #b8b8b8;
}

.share__Modal::-webkit-scrollbar-thumb:hover {
  background: #005734;
}

.share__Modal__CLose__Container {
  width: 100%;
}

.share__Modal__CLose__Icon {
  display: block;
  margin-left: auto;
  cursor: pointer;
}

.share__Subtitle__Info {
  color: #a3a1a1;
  font-size: 0.9rem;
}

.share__Items__Container {
  display: flex;
  gap: 2rem;
  align-items: center;
  width: 100%;
}

.share__Item {
  display: flex;
  list-style: none;
  align-items: center;
  justify-content: center;
  border: 1px solid #a3a1a1;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  transition: 0.5s ease-in-out;
}

.share__Item:hover,
.share__Item.active,
.copy__Link__Btn {
  background-color: #005734;
  color: #fff;
}

.copy__Link__Btn {
  font-family: "Poppins", sans-serif;
  letter-spacing: 0.02em;
  outline: none;
  border: none;
  min-width: 5rem;
  min-height: 2.5rem;
  border-radius: 10px;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.copy__Link__Btn:hover {
  filter: brightness(0.85);
}

.link__Wrapper {
  width: 100%;
  background-color: #f5f5f5;
  padding: 1.5%;
  position: relative;
}

.link__Content {
  max-width: 100%;
  overflow-x: auto;
  font-family: "Poppins", sans-serif;
  font-size: 0.8rem;
}

.copy__Link__Icon {
  position: absolute;
  z-index: 2;
  right: 2%;
  top: 40%;
  transform: translateY(-50%);
  border: 1px solid #a3a1a1;
  border-radius: 5px;
  padding: 0.2%;
  background-color: #f5f5f5;
}

.generate__Link__Btn {
  padding-left: 4%;
  padding-right: 4%;
}

.select__Items__Wrapper,
.select__Item {
  width: 100%;
}

.select__Item {
  overflow-y: auto;
  font-family: "Poppins", sans-serif;
  padding: 0 2%;
  scrollbar-width: thin;
}

.select__Item::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

.select__Item::-webkit-scrollbar-track {
    background: #efefef;
}

.select__Item::-webkit-scrollbar-thumb {
    background: #b8b8b8;
}

.select__Item::-webkit-scrollbar-thumb:hover {
    background: #005734;
}

.select__Item option {
  cursor: pointer;
  margin: 0.4rem 0;
  width: 60%;
  padding: 1% 2%;
  border-radius: 2px;
}

.select__Item option:hover,
.select__Item option.active__Item {
  background-color: #005734;
  color: #fff;
}

.select__Items__Text {
  font-family: "Poppins", sans-serif;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.indicator {
  color: #005734;
}

.copy__Link__Btn:disabled,
.copy__Link__Btn[disabled] {
  filter: brightness(0.6);
  cursor: not-allowed;
}

.qr__code__Item {
  width: 100%;
}

.qr__code__Item img {
  width: 50%;
  max-width: 100%;
  display: block;
  margin: 0 auto;
}

.select__Links__Num {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0.3rem 0;
  width: 100%;
}

.select__Links__Num.last__Page {
  flex-direction: column;
  gap: 1rem;
  align-items: flex-start;
}

.select__Links__Num label {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  gap: 0.25rem;
}

.select__Links__Num label span {
  font-size: 0.9rem;
}

.select__Links__Num label input {
  font-family: "Poppins", sans-serif;
  border: 1px solid #a3a1a1;
  outline: none;
  border-radius: 5px;
  padding: 0.35rem 1rem;
  transition: 0.3s ease-in-out;
}

.select__Links__Num label input:focus-within {
  border-color: #005734;
}

.required {
  color: #ff0000;
}

.select__Category {
  font-family: "Poppins", sans-serif;
  border: 1px solid #a3a1a1;
  outline: none;
  border-radius: 5px;
  background: #fff;
  width: 100%;
  padding: 0.35rem 1rem;
}

@media screen and (max-width: 767px) {
  .share__Modal {
    width: 95%;
    gap: 1.5rem;
  }
  .link__Wrapper {
    padding: 5%;
  }
  .select__Links__Num {
    align-items: flex-start;
    flex-direction: column;
    gap: 1rem;
  }
}
