.candidate-task-screen-container {
    --grey-color: #838383;
    --light-grey-color: #E0E0E0;
    --black: #000;
    --green-color: #005734;
    --red-color: #FC3939;
    --yellow-color: #C09F2C;
    --green-color-2: #005734;
    font-family: 'Inter', sans-serif;
}

.candidate-task-screen-container {
    padding: 3% 7%;
}

.candidate-task-screen-container>* {
    color: var(--grey-color);
}

.candidate-task-screen-container .selected-applicant-intro {
    justify-content: flex-start;
}

.candidate-task-screen-container .selected-applicant-intro span {
    margin: 0;
}

.candidate-task-screen-container .add-task-btn {
    margin: 10% auto 50%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    cursor: pointer;
    color: var(--yellow-color);
}

.candidate-task-screen-container .add-task-btn span {
    margin-right: 1%;
}

.candidate-task-screen-container .all__Tasks__Container {
    display: flex;
    flex-direction: column;
}

.candidate-task-screen-container .all__Tasks__Container .tasks__Wrapper {
    margin-left: 5rem;
    max-width: 60%;
}

.candidate-task-screen-container .all__Tasks__Container .task__Details__Item .month__Title {
    margin-top: 8%;
}

.candidate-task-screen-container .task__Indicator {
    background-color: var(--green-color-2);
}

.candidate-task-screen-container .empty__Task__Content {
    font-size: 0.875rem;
}

.candidate-task-screen-container .all__Tasks__Container .react-calendar {
    height: max-content;
    border-radius: 1.5rem;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4);
    border: none;
    max-width: 18.8rem;
}

.candidate-task-screen-container .all__Tasks__Container .task__Title {
    color: var(--green-color-2);
    font-family: "Poppins", sans-serif;
    font-weight: 500;
    letter-spacing: 0.05em;
    font-size: 0.875rem;
    margin-bottom: 10%;
    color: var(--grey-color);
}

.candidate-task-screen-container .subproject__Div__Wrapper {
    width: 100%;
    margin: 0 auto 3rem;
}

.candidate-task-screen-container .subproject__Div__Wrapper .subproject__Div {
    margin-left: auto;
    width: max-content;
    display: flex;
    align-items: center;
    gap: 2rem;
}

.candidate-task-screen-container .subproject__Div__Wrapper .subproject__Div .subproject__Custom__Select {
    padding: 0.3rem 0.5rem;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    gap: 0.5rem;
    border: none;
    background-color: #fff;
    border: 1px solid #005734;
    border-radius: 6px;
    font-family: 'Poppins', sans-serif;
    font-size: 0.8rem;
    position: relative;
    transition: 0.3s all ease-in-out;
    width: max-content;
}

.candidate-task-screen-container .subproject__Div__Wrapper .subproject__Div .subproject__Custom__Select .select_ {
    display: flex;
    align-items: center;
    gap: 1rem;
    cursor: pointer;
}

.candidate-task-screen-container .subproject__Div__Wrapper .subproject__Div .subproject__Custom__Select .select_ svg {
    font-size: 0.8rem;
}

.candidate-task-screen-container .subproject__Div__Wrapper .subproject__Div .subproject__Custom__Select .task__Select__Subproject .task__Search__Sub {
    box-shadow: none;
    max-width: 12.5rem;
    animation: fadeIn 0.3s ease-in-out;
}

.candidate-task-screen-container .subproject__Div__Wrapper .subproject__Div .subproject__Custom__Select .task__Select__Subproject .task__Search__Sub:focus-within {
    border-color: #fff;
}

@media (min-width: 768px) and (max-width: 991.98px) {
    .candidate-task-screen-container .applicant-title-text {
        margin-bottom: 2%;
    }

    .candidate-task-screen-container .selected-applicant-intro {
        margin-top: 2%;
    }

    .candidate-task-screen-container .all__Tasks__Container {
        flex-direction: row;
    }

    .candidate-task-screen-container .all__Tasks__Container .task__Details__Item {
        margin-left: 4%;
        width: 60%;
    }

    .candidate-task-screen-container .all__Tasks__Container .task__Details__Item .month__Title {
        margin: 0 0 2.5%;
    }

    .candidate__Week__Log__Detail {
        width: 82%;
        margin: 1.5rem auto 2rem;
    }
}

@media (min-width: 992px) {

    .candidate-task-screen-container .add-task-btn,
    .candidate-task-screen-container .all__Tasks__Container {
        width: 82%;
        margin: 3% auto;
    }

    .candidate-task-screen-container .add-task-btn {
        margin-bottom: 11%;
    }

    .candidate-task-screen-container .all__Tasks__Container {
        margin-top: 0;
        flex-direction: row;
        padding-bottom: 10%;
    }

    .candidate-task-screen-container .all__Tasks__Container .task__Details__Item {
        margin-left: 2%;
        width: 80%;
    }

    .candidate-task-screen-container .all__Tasks__Container .task__Details__Item .month__Title {
        margin: 0 0 2%;
    }

    .candidate-task-screen-container  {
        padding-bottom: 250px;
    }

    .candidate__After__Selection__Screen .candidate-task-screen-container .project-details-container {
        margin-bottom: 1rem;
    }

    .candidate-task-screen-container .subproject__Div__Wrapper {
        width: 82%;
        margin: 0 auto 3rem;
    }

    .candidate-task-screen-container .subproject__Div__Wrapper .subproject__Div {
        margin-left: auto;
        width: max-content;
        display: flex;
        align-items: center;
        gap: 2rem;
    }

    .candidate-task-screen-container .subproject__Div__Wrapper .subproject__Div .subproject__Custom__Select {
        padding: 0.3rem 0.5rem;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        gap: 0.5rem;
        border: none;
        background-color: #fff;
        border: 1px solid #005734;
        border-radius: 6px;
        font-family: 'Poppins', sans-serif;
        font-size: 0.8rem;
        position: relative;
        transition: 0.3s all ease-in-out;
        width: max-content;
    }

    .candidate-task-screen-container .subproject__Div__Wrapper .subproject__Div .subproject__Custom__Select .select_ {
        display: flex;
        align-items: center;
        gap: 1rem;
        cursor: pointer;
    }

    .candidate-task-screen-container .subproject__Div__Wrapper .subproject__Div .subproject__Custom__Select .select_ svg {
        font-size: 0.8rem;
    }

    .candidate-task-screen-container .subproject__Div__Wrapper .subproject__Div .subproject__Custom__Select .task__Select__Subproject .task__Search__Sub {
        box-shadow: none;
        max-width: 12.5rem;
        animation: fadeIn 0.3s ease-in-out;
    }

    .candidate-task-screen-container .subproject__Div__Wrapper .subproject__Div .subproject__Custom__Select .task__Select__Subproject .task__Search__Sub:focus-within {
        border-color: #fff;
    }

    .candidate__Week__Log__Detail {
        width: 82%;
        margin: 1.5rem auto 2rem;
    }
}


@media screen and (max-width:768px) {
    .candidate-task-screen-container {
        padding-bottom: 15rem !important;
    }

    .candidate-task-screen-container .project-details-container {
        margin-top: 2rem;
        width: 100%;
        margin: 2rem auto;
        /* padding-bottom: 2rem; */
        /* padding-top: 2rem; */
        flex-direction: column;
        gap: 1rem;
        align-items: flex-start;
    }

    .all__Tasks__Container .task__Title {
        margin-bottom: 0;
    }

    .candidate-task-screen-container .all__Tasks__Container .react-calendar {
        margin: auto;
    }

    .candidate-task-screen-container .all__Tasks__Container .tasks__Wrapper {
        width: 90%;
        margin: 2rem auto;
    }

    .candidate-task-screen-container .all__Tasks__Container .tasks__Wrapper p {
        margin-bottom: 0;
    }

    .candidate__Week__Log__Detail {
        width: 90%;
        margin-bottom: 2rem;
        margin-left: auto;
        margin-right: auto;
    }
}


@keyframes fadeIn {
    from {
        opacity: 0;
    } to {
        opacity: 1;
    }
}