.candidate-task-screen-container {
  --grey-color: #838383;
  --light-grey-color: #e0e0e0;
  --black: #000;
  --green-color: #005734;
  --red-color: #fc3939;
  --yellow-color: #c09f2c;
  font-family: "Inter", sans-serif;
}

.candidate-task-screen-container {
  padding: 0;
  padding-top: 3%;
  padding-bottom: 3%;
}

.candidate-task-screen-container>* {
  color: var(--grey-color);
}

.candidate-task-screen-container .selected-applicant-intro {
  justify-content: flex-start;
}

.candidate-task-screen-container .selected-applicant-intro span {
  margin: 0;
}

.candidate-task-screen-container .add-task-btn {
  margin: 10% auto 50%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  cursor: pointer;
  color: var(--yellow-color);
}

.candidate-task-screen-container .add-task-btn span {
  margin-right: 1%;
}

.candidate-task-screen-container .all__Tasks__Container {
  display: flex;
  flex-direction: column;
}

.candidate-task-screen-container .all__Tasks__Container .task__Details__Item .month__Title {
  margin-top: 8%;
}

.candidate-task-screen-container .task__Indicator {
  background-color: var(--green-color);
}

.candidate-task-screen-container .empty__Task__Content {
  font-size: 0.875rem;
}

.candidate-task-screen-container .all__Tasks__Container .react-calendar {
  height: max-content;
}

.add__btn {
  padding: 0;
}

@media (min-width: 768px) and (max-width: 991.98px) {
  .candidate-task-screen-container .applicant-title-text {
    margin-bottom: 2%;
  }

  .candidate-task-screen-container .selected-applicant-intro {
    margin-top: 2%;
  }

  .candidate-task-screen-container .all__Tasks__Container {
    flex-direction: row;
  }

  .candidate-task-screen-container .all__Tasks__Container .task__Details__Item {
    margin-left: 4%;
    width: 60%;
  }

  .candidate-task-screen-container .all__Tasks__Container .task__Details__Item .month__Title {
    margin: 0 0 2.5%;
  }
}

@media (min-width: 992px) {
  .task-bar {
    margin: 3% 5%;
  }

  .candidate-task-screen-container .add-task-btn,
  .candidate-task-screen-container .all__Tasks__Container {
    width: 82%;
    margin: 3% auto;
  }

  .candidate-task-screen-container .add-task-btn {
    margin-bottom: 11%;
  }

  .candidate-task-screen-container .all__Tasks__Container {
    margin-top: 0;
    flex-direction: row;
    padding-bottom: 10%;
  }

  .candidate-task-screen-container .all__Tasks__Container .task__Details__Item {
    margin-left: 2%;
    width: 80%;
  }

  .candidate-task-screen-container .all__Tasks__Container .task__Details__Item .month__Title {
    margin: 0 0 2%;
  }
}

@media screen and (max-width:886px) {
  .title__Item {
    font-size: 1rem;
  }

  .staff__Jobs__Layout__Content__Container .jobs__Layout__Content {
    margin-top: 0;
    padding-top: 0;
  }

  .staff__Jobs__Layout__Content__Container .candidate-task-screen-container {
    margin-top: 0%;
  }

  .candidate-task-screen-container {
    padding-top: 0;
  }

  .staff__Jobs__Layout__Content__Container .candidate-task-screen-container {
    margin-top: 4%;
    width: 90%;
    margin: auto;
  }

  .task__Details__Item {
    width: 100%;
    /* margin-left: 1.1rem; */
  }

  .candidate-task-container .approve__Task__Btn {
    width: 9rem;
  }

  .container {
    justify-content: center;
  }

  .title__Navigation__Bar__Container .title__Item h1 {
    font-size: 1.2rem;
  }
}