.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(
    0,
    0,
    0,
    0.5
  ); /* Black background with opacity (0.8) */
  z-index: 9990; /* Adjust the z-index value to ensure the overlay appears on top */
  font-family: Poppins;
  .delete_confirmation_container {
    z-index: 9999;
    position: absolute;
    background-color: white;
    padding: 40px;
    // padding-bottom: 20px;
    width: 450px;
    border-radius: 10px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-height: 400px;
    text-align: center;
    overflow-y: auto;
    
    .buttons {
      display: flex;
      align-items: flex-end;
      justify-content: center;
    }

    button{
      margin:15px 10px 0;
      width: 70px;
      height: 27px;
      background-color: #fff;
      border: 1px solid #D2122E;
      color: #D2122E;
      border-radius: 5px;
      cursor: pointer;
    }
    .delete{
      background-color: #D2122E;
      color: #fff;

    }
    .extra__Detail {
      font-size: 0.9rem;
      display: block;
      width: 100%;
      margin-bottom: 0.8rem;
      text-align: left;
    }
    label {
      display: flex;
      align-items: flex-start;
      gap: 0.15rem;
      flex-direction: column;
      font-family: 'Poppins', sans-serif;
      
      span {
        font-size: 0.9rem;
      }
      
      input, textarea {
        width: 100%;
        font-family: 'Poppins', sans-serif;
        padding: 0.15rem 0.4rem;
        outline: none;
        transition: 0.3s ease-in-out;
        border-radius: 4px;
        border: none;
        border: 1px solid #808080;
        resize: none;

        &:focus-within {
          border-color: #005734;
        }
      }
    }

    @media screen and (max-width: 767px) {
      width: 90%;
      padding-left: 18px;
      padding-right: 18px;
    }
  }
}
