.container {
  width: 80%;
  margin: auto;
  flex-flow: wrap;
  display: flex;
  gap: 20px;
  .first-button {
    width: 45%;
    height: 200px;
    border: none;
    background: linear-gradient(to right, 60% white, 40% green);
  }
  @media (max-width: 798px) {
    width: 90%;
  }
}
.board {
  font-family: "Dosis";
  font-size: 2.2rem;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  line-height: 1.5;
  text-shadow: 1px 1px #666, 3px 3px #000;

  width: 250px;
  height: 350px;
  padding: 20px;
  overflow: hidden;
  color: #fff;
  border: 10px solid #eee;

  background: linear-gradient(#0e0e0e 65%, #030303 66%) top 0.03em center / 100%
    0.3em repeat-y;
  box-shadow: 0px -1px 0px 2px #aaa, 0 -7px 5px -5px #000,
    0 -15px 30px -15px #000, 0 3px 4px -2px #000, -60px 30px 70px -70px #000,
    70px 30px 70px -70px #000, 0px 200px 20px rgba(#fff, 0.05),
    inset 0 0 25px #000, inset 0 0 5px #000;

  &:focus {
    outline: none;
  }

  &:after {
    content: "";
    position: absolute;
    width: 100vw;
    height: 100vh;
    left: 0;
    top: calc(50% + 100px);
    z-index: -1;
    box-shadow: 0 -2px 3px rgba(#000, 0.1), inset 0 2px 2px rgba(#000, 0.15),
      inset 0 10px 300px rgba(#000, 0.5);

    &:before {
      content: "";
      position: absolute;
      width: 250px;
      height: 300px;
      left: calc(50% - 370px);
      top: calc(50vh - 30px);
      filter: drop-shadow(20px 10px 20px rgba(0, 0, 0, 0.3));
    }

    .u {
      color: #fd0;
      text-underline-position: under;
    }
  }

  .flexions {
    background: #666;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    overflow: hidden;
  }
}

.teams_data {
  width: 100%;
  padding: 15px;
  @media (max-width: 798px) {
    width: 90%;
  }
  // max-height: calc(100vh - 6.5rem + 40px);
  overflow-y: auto;
  font-family: Poppins;
  padding-bottom: 90px;
  .team{
    h2{
      white-space: nowrap;       
      overflow: hidden;          /* Hide the overflowing text */
      text-overflow: ellipsis;  /* Display ellipsis (...) for the overflow */
      max-width: 100%; 
    }
    
  }
  &::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }

  /* background color of the scrollbar track */
  &::-webkit-scrollbar-track {
    background-color: #f1f1f1;
  }

  /* color of the scrollbar thumb */
  &::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 4px;
  }

  /* color of the scrollbar thumb on hover */
  &::-webkit-scrollbar-thumb:hover {
    background-color: #555;
  }
  > div:first-child {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin-bottom: 66px;
    font-family: Poppins;
    > li {
      position: relative;
      width: 280.799px;
      height: 248.609px;
      flex-shrink: 0;
      border-radius: 5px;
      padding: 15px;
      border: 1px solid rgb(226, 219, 219);
      border-radius: 12px;
      font-family: Poppins;
      list-style: none;
      overflow: hidden;
      transition: 0.3s all;
      &:hover {
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
      }
      h2 {
        color: #000;
        font-size: 23.777px;
        margin: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: 36.284px;
        letter-spacing: 0.288px;
      }
      p {
        margin-left: 12px;
        color: #7e7e7e;
        font-size: 11.697px;
        font-family: Poppins;
        font-style: normal;
        font-weight: 400;
        line-height: 20.888px;
        letter-spacing: 0.117px;
        // asd
      }
      button {
        position: absolute;
        width: 60%;
        font-weight: 600;
        bottom: 20px;
        left: 27px;
        border: none;
        background-color: white;
        display: flex;
        align-items: center;
        gap: 12px;
        font-family: Poppins;
        cursor: pointer;
      }
    }
  }
}
.create-new-team-header {
  > div {
    width: 80%;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 767px) {
      width: 90%;
    }
    > div {
      display: flex;
      gap: 10px;
      button {
        position: relative;
        width: 40px;
        height: 35px;
        border: none;
        outline: none;
        background-color: white;
        box-shadow: rgb(204, 219, 232) 3px 3px 6px 0px inset,
          rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset;
        border-radius: 5px;
        cursor: pointer;
        ::after {
          content: "";
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          bottom: 0;
          box-shadow: #005734 0px 7px 29px 0px;
          background-color: red;
          cursor: pointer;
        }

        span {
          font-weight: 500;
        }
      }
    }
  }
  h1 {
    font-family: Poppins;
    letter-spacing: 0.6px;
  }
}
.create-new-team-btn {
  background-color: #005734;
  color: white;
  font-weight: 600;
  width: 150px;
  height: 40px;
  text-align: center;
  padding: 10px;
  border-radius: 9px;
  display: flex;
  font-weight: 400;
  align-items: center;
  gap: 10px;
  border: none;
  outline: none;
  cursor: pointer;
  width: 120px;

  font-family: Poppins;
  @media (max-width: 798px) {
    border-radius: 50%;
    width: 40px !important	;
    height: 40px;
    span {
      display: none;
    }
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.create-new-team-header {
}
.paragraph-discription {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
}

.blur {
  position: absolute;
  z-index: 0;
  width: calc(100vw - 4.6rem);
  height: calc(100vh - 6.5rem);
  background-color: rgba(0, 0, 0, 0.3);
  top: 0rem;
  left: -210px;
}
